import Info from "../../sections/Info";
import Footer from "../../sections/Footer";
import {Heading, Stack, Text} from "@chakra-ui/react";

export default () => {
    return(
        <div>
            <Stack paddingX='15%' py='70px'>
                <Heading>
                    Servicios de Contabilidad y Finanzas
                </Heading>
                <h4>
                    Anlisis Financiero para la Toma de Decisiones
                </h4>
                <Text fontSize='lg'>
                    La modelacin de escenarios a partir del anlisis de estados financieros es una herramienta
                    fundamental para determinar y analizar la situacin financiera de una organizacin. A travs de un
                    exhaustivo examen de los estados financieros, podemos identificar tendencias, fortalezas y reas de
                    mejora que permitan proponer a la direccin decisiones financieras tanto a corto como a largo plazo.
                </Text>
                <h4>
                    Proyeccin de Ingresos y Elaboracin de Informes Contables
                </h4>
                <Text fontSize='lg'>
                    Parte integral de este proceso es la proyeccin de ingresos, que nos permite anticipar el desempeo
                    financiero futuro de la organizacin. Adems, se elaboran informes contables detallados que resumen
                    el comportamiento econmico de la empresa, proporcionando una visin clara y concisa de su situacin
                    financiera.
                </Text>
                <h4>
                    Anlisis Integral del Negocio y Planificacin Econmica
                </h4>
                <Text fontSize='lg'>
                    En los estudios de factibilidad, se describe minuciosamente el negocio objeto de estudio, analizando
                    los flujos de efectivo y elaborando un plan econmico detallado que gue las operaciones futuras de
                    la organizacin. Se confeccionan fichas de costos y gastos, y se planifica y ejecuta la carga fiscal
                    de manera eficiente.
                </Text>
                <h4>
                    Gestin Contable y Tenedura de Libros
                </h4>
                <Text fontSize='lg'>
                    La tenedura de libros abarca la ejecucin de tareas contables como la gestin de cuentas por
                    cobrar, cuentas por pagar, activos fijos tangibles e inventarios, siguiendo procedimientos
                    preestablecidos con precisin y exactitud. Adems, se recaban, registran y clasifican las
                    operaciones de la organizacin, y se narran en forma escrita los hechos econmicos relevantes.
                </Text>
            </Stack>
            <Info/>
            <Footer/>
        </div>
    )
}
