import {Heading, Stack, Text} from "@chakra-ui/react";
import Info from "../../sections/Info";
import Footer from "../../sections/Footer";

export default () => {
    return(
        <div>
            <Stack paddingX='15%' py='40px'>
                <Heading>
                    Servicios de Creacion de Audiovisuales
                </Heading>
                <h4>
                    Fotografa Publicitaria: Estudio y Exteriores
                </h4>
                <Text fontSize='lg'>
                    En [Nombre de la Empresa], nos especializamos en la creacin de fotografas publicitarias tanto en
                    estudio como en exteriores. Capturamos la esencia de tu marca con imgenes de alta calidad que
                    destacan tus productos o servicios de manera impactante y atractiva.
                </Text>
                <h4>
                    Colchn Fotogrfico: Publicitario e Institucional
                </h4>
                <Text fontSize='lg'>
                    Nuestro equipo experto en fotografa publicitaria ofrece un colchn fotogrfico verstil, adaptado a
                    tus necesidades. Ya sea para promocionar un producto o para transmitir la identidad institucional de
                    tu empresa, creamos imgenes que comunican tu mensaje de forma efectiva y profesional.
                </Text>
                <h4>
                    Videos Publicitarios: Estudio y Exteriores
                </h4>
                <Text fontSize='lg'>
                    Adems de la fotografa, tambin ofrecemos servicios de creacin de videos publicitarios en estudio
                    y en locaciones exteriores. Desde anuncios cortos hasta videos promocionales ms elaborados, nos
                    encargamos de producir contenido audiovisual que capte la atencin de tu audiencia y impulse tu
                    marca.
                </Text>
                <h4>
                    Colchn Videogrfico: Publicitario e Institucional
                </h4>
                <Text fontSize='lg'>
                    Nuestro colchn videogrfico abarca desde videos publicitarios que resaltan tus productos o
                    servicios hasta videos institucionales que transmiten la filosofa y los valores de tu empresa. Con
                    una combinacin de creatividad y profesionalismo, creamos videos que generan impacto y conectan con
                    tu pblico objetivo.
                </Text>
            </Stack>
            <Info/>
            <Footer/>
        </div>
    )
}
